:host {
  display: block;
}

button {
  padding: 0 16px;
  cursor: pointer;
}

.small-size {
  height: 24px;
  border-radius: 12px;
  font-size: 16px;
}

.medium-size {
  height: 48px;
  border-radius: 24px;
  font-size: 16px;
}

.primary-style {
  background-color: $primary-color;
  color: $primary-text-color;
  border-color: $primary-color;
}

.secondary-style {
  color: $primary-text-color;
  border-color: $primary-color;
}

.solid-appearance {
  border: none;

  &:hover {
    background-color: $primary-hover-color;
  }
}

.outline-appearance {
  background-color: transparent;
  color: $primary-color;
  border: 2px solid $primary-color;

  &:hover {
    background-color: $primary-color;
    color: $primary-text-color;
  }
}
